@media (max-width: 767px) {
  .main-nav {
    width: 320px;
  }
  .main-nav__toggle {
    position: absolute;
    padding: 0;
    top: 20px;
    right: 20px;
    z-index: 2;
    display: block;
    width: 40px;
    height: 40px;
    background-color: transparent;
    cursor: pointer;
    border: none;
  }
  .main-nav__toggle:hover,
  .main-nav__toggle:focus {
    background-color: transparent;
    opacity: 0.6;
  }
  .main-nav__toggle:active {
    background-color: transparent;
    opacity: 0.3;
  }
  .main-nav--closed .main-nav__toggle::before,
  .main-nav--closed .main-nav__toggle:active::before {
    content: "";
    position: absolute;
    top: 9px;
    right: 10px;
    width: 20px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 5px;
    right: 19px;
    width: 2px;
    height: 25px;
    background-color: #000000;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:active::before,
  .main-nav--opened .main-nav__toggle:active::after {
    background-color: #ffffff;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav__main-list,
  .main-nav__nav-wrapper {
    margin: 0;
    padding: 0;
    list-style: none;
    background-color: #ffffff;
  }
  .main-nav__new-list {
    display: none;
  }
  .main-nav__link-delivery {
    display: none;
  }
  .main-nav--closed .main-nav__main-list,
  .main-nav--closed .main-nav__nav-wrapper {
    display: none;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
